.note {
  display: -ms-flexbox;
  display: flex;
  background: lightblue;
  width: 100%;
  margin: 0 0 10px 0;
}

.note-text {
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 0;
  padding: 0;
  white-space: pre-wrap;
}

.note-del {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-item-align: end;
      align-self: flex-end;
}


