.task {
  display: -ms-flexbox;
  display: flex;
  background: lightblue;
  width: 100%;
  margin: 0 0 10px 0;
}

.task--completed {
  opacity: 0.3;
}

.task--completed .task-text {
  text-decoration: line-through;
}

.task-done {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.task-text {
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 0;
  padding: 0;
}

.task-del {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-item-align: end;
      align-self: flex-end;
}


